<template>
  <div class="staticReports">
    <el-container>
      <!--<el-header>
        <div class="staticReports">
          <el-row class="staticReports-header">
            <el-col :span="24"><h3 @click="$router.back(-1)">流程报表</h3> > <span>使用量最多流程</span></el-col>
          </el-row>
          <el-row class="staticReports-middle">
            <el-col :span="2"><span>组织选择:</span></el-col>
            <el-col :span="12"><el-input    placeholder="所有"></el-input></el-col>
            <el-col :span="1"> <i class="iconfont icon-organization"></i></el-col>
            <el-col :span="6" style="text-align:right;padding-right:15px;">  <el-button type="primary">  <i class="el-icon-search"></i>查询</el-button></el-col>
            <el-col :span="3">  <el-button type="primary"> <i class="iconfont icon-export"></i>导出</el-button></el-col>
          </el-row>
          <div class="taticReports-content">
            <h4 class="resulter">报表结果</h4>
          </div>
        </div>
      </el-header>-->
      <div class="personReports">
  <h2 class="personReports-title">用户流程处理耗时排名表</h2>
  <div class="reportsStyle">
    <span class="reports-title">报表条件</span>
    <span class="reports-span" @click="collapseReports">
            <span class="reports-collapse">收起</span>
            <i :class="showReportsCondition ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"></i>
          </span>
  </div>
    <div v-show="showReportsCondition" class="reportsConditionForm">
      <el-form :label-position="labelPosition" label-width="160px" :model="formLabelAlign">
        <el-form-item label="人员组织选择:">
          <el-input v-model="formLabelAlign.staffingOrganization" style="width:592px;">
            <i slot="suffix" class="el-input__icon el-icon-date" @click="chosePerson"></i>
          </el-input>
        </el-form-item>
        <el-form-item label="流程选择:">
          <el-input v-model="formLabelAlign.flow" style="width:592px;">
            <i slot="suffix" class="el-input__icon el-icon-date" @click="choseFlow"></i>
          </el-input>
        </el-form-item>
        <el-form-item label="时间段" prop="resource">
          <el-radio-group v-model="formLabelAlign.timeQuantum">
            <el-radio label="本周"></el-radio>
            <el-radio label="本月"></el-radio>
            <el-radio label="本季度"></el-radio>
            <el-radio label="本年"></el-radio>
            <el-radio label="其他:"></el-radio>
          </el-radio-group>
          <!-- v-model="formLabelAlign.date" -->
          <el-date-picker class="datePicker"
                          type="daterange"
                          range-separator="To"
                          start-placeholder="开始日期"
                          end-placeholder="结束日期">
          </el-date-picker>
        </el-form-item>
      </el-form>
      <div class="operating">
        <el-button type="primary" @click="checkOut">查询</el-button>
        <el-button type="primary" @click="resetCheck">重置 </el-button>
      </div>
    </div>

  <div class="reportsStyle">
    <span class="reports-title">报表结果</span>
    <span class="reports-span" @click="collapseForm">
            <span class="reports-collapse">收 起</span>
            <i :class="showReportsResult ? 'el-icon-arrow-up' : 'el-icon-arrow-down'"></i>
          </span>
  </div>
    <div v-show="showReportsResult">
      <el-table class="tableReportsForm"
                :data="tableReportsForm"
                border
                style="width: 100%">
        <el-table-column
          prop="ranking"
          label="排名"
          width="180">
        </el-table-column>
        <el-table-column
          prop="personnel"
          label="人员"
          width="180">
        </el-table-column>
        <!-- prop="averageprocessingtime" -->
        <el-table-column

          label="平均处理耗时">
          <template slot-scope="scope">
            <span>{{scope.row.averageprocessingtime | changeToDay}}</span>
          </template>
        </el-table-column>
        <el-table-column
          prop=""
          label="">
          <template slot-scope="scope">
            <div :style="'width:'+scope.row.averageprocessingtime/maxTime*80+'%;background:blue;'">
              <span style="color:blue;">{{scope.row.averageprocessingtime/maxTime*100 | keepTwoNum}}%</span>
            </div>
          </template>
        </el-table-column>
      </el-table>

      <!-- 图表 -->
      <!-- <el-col :span="12">
        <div id="chartBar" style="width:100%; height:400px;"></div>
      </el-col> -->
    </div>
</div>

    </el-container>
  </div>
</template>

<script>
  // import echarts from 'echarts'
  export default {
    name: 'personReports',
    data () {
      return {
        dialogVisible: false,
        labelPosition: 'left',
        formLabelAlign: {
          staffingOrganization: '',
          flow: '',
          timeQuantum: ''
          // date: ''
        },
        activeNames: ['1'],
        showReportsCondition: true,
        showReportsResult: true,
        // 图表
        // chartBar: null,
        // 表格比例
        // scaleTime: '',
        // 是否可用
        disabledTime: false,
        tableData: [
          {
            sort: '1',
            person: '王五',
            time: '10'
          },
          {
            sort: '2',
            person: '赵柳',
            time: '20'
          },
          {
            sort: '3',
            person: '李四',
            time: '45'
          }
        ],
        // 表格最大时间
        maxTime: '',
        tableReportsForm: [],
        tableReportsFormData:[
          {
            averageprocessingtime: 27,
            ranking: '1',
            personnel: 'ALCDC01.陈洁夫'
          },
          {
            averageprocessingtime: 10,
            ranking: '2',
            personnel: 'ALCDB01.贾磊'
          },
          {
            averageprocessingtime: 30,
            ranking: '3',
            personnel: 'ALCDA01.刘庆辉'
          },
          {
            averageprocessingtime: 180,
            ranking: '4',
            personnel: 'ALCDE01.周兴'
          },
          {
            averageprocessingtime: 20,
            personnel: 'ALCDD01.黄平显',
            ranking: '5'
          },
          {
            averageprocessingtime: 47,
            ranking: '6',
            personnel: 'ALCDA01.刘庆辉'
          }
        ]
      }
    },
    computed: {

    },
    filters: {
      // 过滤器, 数字保小数位数
      keepTwoNum(value) {
        value = Number(value);
        return value.toFixed(0)
      },
      // 转化日期格式
      changeToDay (StatusMinute) {
        StatusMinute = Number(StatusMinute);
        var day=parseInt(StatusMinute/60/24);
        var hour=parseInt(StatusMinute/60%24);
        var min= parseInt(StatusMinute % 60);
        StatusMinute="";
        if (day > 0)
        {
          StatusMinute= day + "天";
        }
        if (hour>0)
        {
          StatusMinute += hour + "小时";
        }
        if (min>0)
        {
          StatusMinute += parseFloat(min) + "分钟";
        }
        return StatusMinute;
      }
    },
    mounted () {

    },
    methods: {
      // 获取最大时间
      charts (data) {
        if (!data) return;
        var dataTime = data;
        var max = '';
        for (let i = 0; i < dataTime.length; i++) {
          if (max < dataTime[i].averageprocessingtime) {
            max = dataTime[i].averageprocessingtime;
          }
        }
        this.maxTime = max;
      },
      // 获取数据
      getFormData(obj) {
        this.tableReportsForm = this.tableReportsFormData;
        var data = this.tableReportsForm;
        this.charts(data);
      },
      // 收起报表
      collapseReports (val) {
        this.showReportsCondition = !this.showReportsCondition;
      },
      collapseForm (val) {
        this.showReportsResult = !this.showReportsResult;
      },
      chosePerson () {
      },
      choseFlow () {
      },
      checkOut () {
        this.getFormData(this.formLabelAlign);
      },
      resetCheck () {
        this.formLabelAlign.staffingOrganization = '';
        this.formLabelAlign.flow = '';
        this.formLabelAlign.timeQuantum = '';
        // this.formLabelAlign.date = '';
      }
    }
  }
</script>
<style lang="scss" scoped>
  .personReports {
    .personReports-title {
      /*text-align: center;*/
    }
    .reportsStyle {
      margin: 10px;
      .reports-title {
        font-size: 18px;
        font-weight: bolder;
        text-align: left;
        padding-left: 5px;
        border-left: 13px solid blue;
      }
      .reports-span {
        cursor: pointer;
        .reports-collapse {
          // background: gray;
          padding: 0;
          margin-left: 10px;
          font-size: 14px;
          color: blue;
          border-bottom: 2px solid blue;
          & > i {
            // background: red;
            border: none;
          }
        }
      }

    }
    .reportsConditionForm {
      margin: 10px 30px;
      .datePicker {
        margin-left: 20px;;
      }
      .operating {
        text-align: right;
        margin-right: 130px;
      }
    }
    .tableForm {
      margin: 10px 30px;
    }
  }
  .staticReports {
    font-family: "Microsoft YaHei";

    .el-header {
      padding: 0;
      .staticReports {
        padding: 12px 24px 0px 13px;
        .taticReports-content{
          height: 100%;
          background: #ffffff;
          margin-top: 15px;
          border-radius: 10px;
          padding: 25px 13px 0 13px;
          .resulter{
            border-left:3px solid #00a1f1;
            height: 25px;
            line-height: 25px;
            padding-left: 15px;

          }
        }
        .staticReports-middle{
          height: 65px;
          margin-top: 5px;
          line-height: 65px;
          .staticReports-middle span{
            color: #cccccc;
          }
          .el-icon-rank{
            color: #22a5fd;
            font-size: 25px;
            line-height: 40px;
            margin-left: 10px;
          }
        }
        text-align: left;
        .staticReports-header ,.staticReports-middle{
          background: #ffffff;
          border-radius: 10px;
          padding-left: 10px;

          h3
          {
            display: inline-block;
            line-height: 60px;
          }
          h3:nth-child(1){
            color: #22a5fd;
            cursor: pointer;
          }
        }
      }
    }
  }

</style>



